<template>
	<view class="container mine">
		<cu-head></cu-head>
		<view class="mine-head">
			<image :src="userInfo.avatar" class="mine-head-image" mode=""></image>
			<view class="mine-head-box">
				<view class="mine-head-box-title">
					{{userInfo.nick_name||'未设置昵称'}}
				</view>
				<view class="mine-head-box-phone">
					{{userInfo.phone?(userInfo.phone.substring(0,3)+'****'+userInfo.phone.substring(7,12)):''}}
				</view>
			</view>
			<!-- <text class="cuIcon-right mine-head-icon"></text> -->
		</view>
		<view class="mine-notice">
			<view class="mine-notice-name">
				资产地址：
			</view>
			<view class="mine-notice-title">
				{{userInfo.public_key}}
			</view>
			<image src="https://file.yuanyinfu.com/front-end-lib/copy-icon.png" class="mine-notice-copy" mode="" @click='copy(userInfo.public_key)'>
			</image>
		</view>


		<view class="mine-list">
			<view v-if="userInfo.identity_type===2" class="mine-list-item" @click="goToInviteFriends">
				<!-- <image src="../../static/yaoqing.png" class="mine-list-item-image" mode=""></image> -->
				<text class="mine-list-item-image mine-list-item-bgimage"></text>
				邀请好友
				<text class="cuIcon-right mine-head-icon" style="color: #fff;"></text>
			</view>
			<view v-if="userInfo.identity_type===2" class="mine-list-item" @click="goToMyTeam">
				<image src="../../static/team.png" class="mine-list-item-image" mode=""></image>
				我的团队
				<text class="cuIcon-right mine-head-icon" style="color: #fff;"></text>
			</view>
			<view class="mine-list-item" @click="goToCollections">
				<image src="../../static/mine.png" class="mine-list-item-image" mode=""></image>
				我的专辑
				<text class="cuIcon-right mine-head-icon" style="color: #fff;"></text>
			</view>
			<view class="mine-list-item" @click="goToOrder">
				<image src="../../static/order.png" class="mine-list-item-image" mode=""></image>
				专辑订单
				<text class="cuIcon-right mine-head-icon" style="color: #fff;"></text>
			</view>
			<view class="mine-list-item" @click="goCustomer">
				<image src="../../static/kefu.png" class="mine-list-item-image" mode=""></image>
				联系客服
				<text class="cuIcon-right mine-head-icon" style="color: #fff;"></text>
			</view>
		</view>


		<view class="logout" @click="goToLogin">
			退出登录
		</view>
	</view>
</template>

<script>
	import {
		h5_user_info
	} from '../../request/api.js'
	export default {
		data() {
			return {
				userInfo: {}
			}
		},
		methods: {
			copy(data) {
				uni.setClipboardData({
					data,
					success() {
						uni.showToast({
							title: '内容已复制'
						})
					},
					fail(e) {
						console.log(e)
					}
				})
			},
			goToCollections() {
				uni.navigateTo({
					url: '/pages/collections/collections'
				})
			},
			goToOrder() {
				uni.navigateTo({
					url: '/pages/orderList/orderList'
				})
			},
			goToLogin() {
				this.$store.commit('user/set_token', '')
				this.$store.commit('user/set_userInfo', '')
				uni.redirectTo({
					url: '/pages/login/login'
				})
			},
			goCustomer() {
				uni.navigateTo({
					url: '/pages/customerService/customerService'
				})
			},
			// 去邀请好友
			goToInviteFriends() {
				uni.navigateTo({
					url: '/pages/inviteFriends/inviteFriends'
				})
			},
			// 去我的团队
			goToMyTeam() {
				uni.navigateTo({
					url: '/pages/myTeam/myTeam'
				})
			},
			async getUserInfo() {
				if (this.$store.state.user.userInfo) {
					this.userInfo = this.$store.state.user.userInfo
				} else {
					const res = await this.$get(h5_user_info)
					if (res.code !== 0) {
						return uni.showToast({
							title: res.msg,
							icon: 'error'
						})
					}
					this.userInfo = res.data
					this.$store.commit('user/set_userInfo', res.data)
				}
			}

		},
		onLoad() {
			this.getUserInfo()
		}
	}
</script>

<style lang="scss" scoped>
	.container {
		padding: 0;
	}

	.mine {
		height: 100vh;
		position: relative;

		.logout {
			font-style: normal;
			font-weight: 500;
			font-size: 28rpx;
			line-height: 40rpx;
			/* identical to box height */

			text-align: center;

			color: #D10910;
			// position: absolute;
			// bottom: 240rpx;
			// left: 0;
			padding-top: 360rpx;
			padding-bottom: 50rpx;
			width: 100%;
		}

		&-head {
			display: flex;
			align-items: center;
			padding-top: 40rpx;

			&-image {
				width: 110rpx;
				height: 110rpx;
				border-radius: 50%;
				margin-left: 32rpx;
			}

			&-box {
				margin-left: 24rpx;
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				height: 90rpx;

				&-title {
					font-family: 'PingFang SC';
					font-style: normal;
					font-weight: 400;
					font-size: 16px;
					line-height: 22px;
					color: #ECECEC;

				}

				&-phone {
					font-weight: 400;
					font-size: 12px;
					line-height: 17px;
					color: #AEAEAE;

				}
			}

			&-icon {
				margin-left: auto;
			}
		}

		&-notice {
			display: flex;
			align-items: center;
			background: #1D1D1D;

			border: 1.3rpx solid #2F2F2F;

			border-radius: 10.6rpx;
			margin-top: 16rpx;
			width: 892rpx;
			height: 80rpx;
			transform-origin: 0 0;
			transform: scale(0.75) translate(53rpx, 0);

			&-name {
				background: linear-gradient(90deg, #9C8746 0%, #645735 93.2%);
				border-radius: 6rpx;
				font-size: 24rpx;
				width: 160rpx;
				height: 65rpx;
				margin: 6rpx;
				display: flex;
				align-items: center;
				justify-content: center;
			}

			&-title {
				flex: 1;
				overflow: hidden;
				padding: 10rpx;
				font-weight: 500;
				font-size: 18rpx;
				color: #AEAEAE;

				white-space: nowrap;
				text-overflow: ellipsis;
				overflow: hidden;
			}

			&-copy {
				color: #B19E63;
				width: 32rpx;
				height: 32rpx;
				margin-right: 28rpx;
				margin-left: 20rpx;
			}
		}

		&-list {
			padding: 18rpx 20rpx 0 20rpx;

			&-item {
				display: flex;
				align-items: center;
				box-sizing: border-box;
				border-bottom: 1rpx solid #363636;
				padding: 0 20rpx;
				height: 88.62rpx;
				font-weight: 500;
				font-size: 24rpx;
				line-height: 17px;
				color: #9B8751;


				&-image {
					width: 48rpx;
					height: 48rpx;
					margin-right: 20rpx;
				}

				&-bgimage {
					background: url('../../static/yaoqing.png') 4rpx 0/48rpx 57.23rpx no-repeat;
				}
			}

		}
	}
</style>
